<template>
  <div class="container">
    <!-- 买家信息 -->
    <div class="container__top">
      <div class="container__top__header">
        <div @click="handleBackClick" class="container__top__header__back iconfont">&#xe7eb;</div>
        <div class="container__top__header__text">确认订单</div>
      </div>
      <div class="container__top__receive">
        <div class="container__top__receive__left">
          <div class="container__top__receive__left__title">收货地址</div>
          <div class="container__top__receive__left__address">北京理工大学国防科技学院</div>
          <div class="container__top__receive__left__info">
            <div class="container__top__receive__left__info__name">守仁</div>
            <div class="container__top__receive__left__info__phone">12345678912</div>
          </div>
        </div>
        <div class="container__top__receive__right">
          <div class="container__top__receive__right__go iconfont">&#xe6a3;</div>
        </div>
      </div>
    </div>
    <!-- 商品列表 -->
    <div class="container__order">
      <div class="container__order__shop-name">{{shopName}} </div>
      <div class="container__order__info">
        <!-- 两种以内商品直接显示 -->
        <div class="container__order__info__item" v-for="item in Object.keys(cartItems).slice(0,2)" :key="item.id">
          <div class="container__order__info__item__left">
            <img :src="cartItems[item].imgUrl" alt="" class="container__order__info__item__left__img">
          </div>
          <div class="container__order__info__item__right">
            <div class="container__order__info__item__right__title">{{cartItems[item].name}}</div>
            <div class="container__order__info__item__right__price">
              <div class="container__order__info__item__right__price__price-number">{{`￥${cartItems[item].promotionPrice}*${cartItems[item].count}`}}</div>
              <div class="container__order__info__item__right__price__price-sum">{{`￥${(cartItems[item].promotionPrice*cartItems[item].count).toFixed(2)}`}}</div>
            </div>
          </div>
        </div>
        <!-- 超过两种商品隐藏起来 -->
        <template v-if="!showMore">
          <div class="container__order__info__item" v-for="item in Object.keys(cartItems).slice(2)" :key="item.id">
            <div class="container__order__info__item__left">
              <img :src="cartItems[item].imgUrl" alt="" class="container__order__info__item__left__img">
            </div>
            <div class="container__order__info__item__right">
              <div class="container__order__info__item__right__title">{{cartItems[item].name}}</div>
              <div class="container__order__info__item__right__price">
                <div class="container__order__info__item__right__price__price-number">{{`￥${cartItems[item].promotionPrice}*${cartItems[item].count}`}}</div>
                <div class="container__order__info__item__right__price__price-sum">{{`￥${(cartItems[item].promotionPrice*cartItems[item].count).toFixed(2)}`}}</div>
              </div>
            </div>
          </div>
        </template>
      </div>
      <div @click="showMoreClick" v-if="showMore && Object.keys(cartItems).length > 2" class="container__order__more iconfont">共计商品{{ Object.keys(cartItems).length }}种/{{ sumNum }}件&nbsp;&nbsp;&#xe69b;</div>
    </div>
    <!-- 订单页底部 -->
    <div class="container__bottom">
      <div class="container__bottom__left">实付金额<span class="container__bottom__left__amount">￥{{amount}}</span></div>
      <div class="container__bottom__right" @click="submitOrderClick">提交订单</div>
    </div>
    <!-- 订单页弹框 -->
    <div class="container__mask" v-if="maskShow" @click.self="cancelMaskClick">
      <div class="container__mask__panel" v-if="panelShow">
        <p class="container__mask__panel__title">确认要离开收银台？</p>
        <p class="container__mask__panel__message">请尽快完成支付，否则将被取消</p>
        <div class="container__mask__panel__buttons">
          <div class="container__mask__panel__buttons__left" @click="cancelOrderClick">取消订单</div>
          <div class="container__mask__panel__buttons__right" @click="confirmPayClick">确认支付</div>
        </div>
      </div>
      <div class="container__mask__success-pay" v-if="payShow">
        <div class="container__mask__success-pay__back iconfont" @click="cancelIconClick">&#xe633;</div>
        <div class="container__mask__success-pay__correct iconfont">&#xe604;</div>
        <div class="container__mask__success-pay__text">支付成功，等待配送</div>
      </div>
    </div>
  </div>
</template>
<script>
import { computed, ref } from '@vue/runtime-core'
import { useRoute, useRouter } from 'vue-router'
import { useStore } from 'vuex'
import { useBackRouterEffect } from '../../effects/backEffect'
const useOrderEffect = () => {
  const router = useRoute()
  const store = useStore()
  const shopId = router.params.shopId
  const cartItems = store.state.cartData[shopId].itemList
  const shopName = store.state.cartData[shopId].shopName
  const amount = computed(() => {
    let count = 0
    for (const key in cartItems) {
      count += cartItems[key].count * cartItems[key].promotionPrice
    }
    return count.toFixed(2)
  })
  let sumNum = 0
  for (const key in cartItems) {
    sumNum += cartItems[key].count
  }
  return { shopName, cartItems, amount, sumNum }
}
// 弹框显示的逻辑
const maskShowEffect = () => {
  const store = useStore()
  const route = useRoute()
  const router = useRouter()
  const shopId = route.params.shopId
  const maskShow = ref(false)
  const panelShow = ref(false)
  const payShow = ref(false)
  // 点击蒙层
  const cancelMaskClick = () => {
    maskShow.value = false
    panelShow.value = false
    if (payShow.value) {
      payShow.value = false
      router.push({ name: 'OrderList' })
    }
  }
  // 取消支付
  const cancelOrderClick = () => {
    maskShow.value = false
    panelShow.value = false
    payShow.value = false
  }
  // 提交订单
  const submitOrderClick = () => {
    maskShow.value = true
    panelShow.value = true
    payShow.value = false
  }
  // 支付点击
  const confirmPayClick = () => {
    maskShow.value = true
    panelShow.value = false
    payShow.value = true
    // 支付后购物车清空
    store.commit('clearCart', { shopId })
  }
  // 叉号点击
  const cancelIconClick = () => {
    payShow.value = false
    panelShow.value = false
    maskShow.value = false
    // 跳到订单页
    router.push({ name: 'OrderList' })
  }
  return { maskShow, cancelMaskClick, cancelOrderClick, submitOrderClick, payShow, panelShow, confirmPayClick, cancelIconClick }
}
// 商品种类2以上显示和隐藏的逻辑
const useShowMoreEffect = () => {
  const showMore = ref(true)
  const showMoreClick = () => {
    showMore.value = false
  }
  return { showMore, showMoreClick }
}
export default {
  name: 'OrderConformation',
  setup () {
    const { shopName, cartItems, amount, sumNum } = useOrderEffect()
    const handleBackClick = useBackRouterEffect()
    const { showMore, showMoreClick } = useShowMoreEffect()
    const { maskShow, cancelMaskClick, cancelOrderClick, submitOrderClick, payShow, panelShow, confirmPayClick, cancelIconClick } = maskShowEffect()
    return { maskShow, cancelMaskClick, cancelOrderClick, submitOrderClick, shopName, cartItems, handleBackClick, amount, showMore, showMoreClick, sumNum, payShow, panelShow, confirmPayClick, cancelIconClick }
  }
}
</script>
